/* ARROW */

.arrow {
  opacity: 0;
  position: absolute;
  left: 50%;
  top: 65%;
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
}

.arrow-first {
  -webkit-animation: arrow-movement 3s ease-in-out infinite;
  animation: arrow-movement 3s ease-in-out infinite;
}

.arrow:before,
.arrow:after {
  background: #fff;
  content: '';
  display: block;
  height: 3px;
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
}

.arrow:before {
  -webkit-transform: rotate(45deg) translateX(-23%);
  transform: rotate(45deg) translateX(-23%);
  -webkit-transform-origin: top left;
  transform-origin: top left;
}

.arrow:after {
  -webkit-transform: rotate(-45deg) translateX(23%);
  transform: rotate(-45deg) translateX(23%);
  -webkit-transform-origin: top right;
  transform-origin: top right;
}

@-webkit-keyframes arrow-movement {
  0% {
    opacity: 0;
    top: 60%;
  }
  70% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes arrow-movement {
  0% {
    opacity: 0;
    top: 60%;
  }
  70% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

/* Prism Widget */

.single {
  width: 450px !important;
  background: red;
  /*-webkit-animation: glow 1.7s ease-in-out infinite alternate;*/
  /*animation: glow 1.7s ease-in-out infinite alternate;*/
}

.prism-shape {
  width: 150px;
  height: 150px;
  border-bottom: 3px solid grey;
  position: relative;
  margin: 2rem auto 5rem;
}
.prism-shape:before,
.prism-shape:after {
  content: '';
  position: absolute;
  width: 150px;
  height: 3px;
  bottom: 0;
  background: grey;
  z-index: 3;
}
.prism-shape:before {
  -webkit-transform: rotate(60deg) translateX(-47%);
  transform: rotate(60deg) translateX(-47%);
  right: -72px;
}
.prism-shape:after {
  -webkit-transform: rotate(-60deg) translateX(47%);
  transform: rotate(-60deg) translateX(47%);
  left: -72px;
}
.prism-shape .white-streak {
  height: 3px;
  width: 300px;
  position: absolute;
  left: -278px;
  background: grey;
  -webkit-transform: rotate(-13deg) translateY(calc(100% + 110px));
  transform: rotate(-13deg) translateY(calc(100% + 110px));
  border-radius: 10px 0 0 10px;
}
.prism-shape .prism-wrapper {
  position: relative;
  height: 100px;
  width: 400px;
  -webkit-perspective: 150px;
  perspective: 150px;
  margin: 80px auto auto -120px;
  -webkit-transform: rotate(36deg);
  transform: rotate(36deg);
}
.prism-shape .prism-clip-wrapper {
  width: 700px;
  height: 520px;
  position: absolute;
  display: flex;
  overflow: hidden;
  -webkit-transform: rotate(-31deg);
  transform: rotate(-31deg);
  left: 153px;
  top: -202px;
  z-index: -1;
}

.long {
  width: 400px !important;
}

@-webkit-keyframes glow {
  to {
    width: 100%;
    flex-grow: 1;
  }
}

@keyframes glow {
  to {
    width: 100%;
    flex-grow: 1;
  }
}
.prism-items {
  width: 100%;
  height: 100%;
  position: absolute;
  -webkit-transform: rotateY(-20deg);
  transform: rotateY(-20deg);
  display: flex;
  flex-direction: column;
}
.prism-items .prism-colors {
  width: 310px;
  flex-grow: 1;
  -webkit-filter: blur(5px);
  filter: blur(5px);
}
.prism-items .prism-colors:nth-child(1) {
  background: red;
}
.prism-items .prism-colors:nth-child(2) {
  background: orange;
}
.prism-items .prism-colors:nth-child(3) {
  background: yellow;
}
.prism-items .prism-colors:nth-child(4) {
  background: green;
}
.prism-items .prism-colors:nth-child(5) {
  background: blue;
}
.prism-items .prism-colors:nth-child(6) {
  background: indigo;
}
.prism-items .prism-colors:nth-child(7) {
  background: violet;
}

.lens {
  background-color: #dedede;
  border-radius: 10px;
  height: 170px;
  width: 10px;
  position: absolute;
  top: -57px;
  right: 91px;
}

.camera-filter {
  width: 7px;
  right: 84px;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.1s linear;
  z-index: 1;
}

.camera-filter-active {
  top: -34px;
  visibility: visible;
  opacity: 1;
  transition: 0.2s;
}

/* End Prism Widget*/

