/* Filters */

/* Intro */
.intro_filter {
  background-position: center;
  border-radius: 10px;
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: 100%;
  background-blend-mode: multiply;
  mix-blend-mode: screen; /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */ /* Firefox 3.5 - 3.6 */
  box-shadow: 0px 0px 11px #ccc;
  /*display: none;*/
}

/* Six Color Filter */
.image_filter {
  background-position: center;
  border-radius: 10px;
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: 100%;
  background-blend-mode: multiply;
  mix-blend-mode: screen; /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */ /* Firefox 3.5 - 3.6 */
  box-shadow: 3px 3px 6px #ccc;
  display: none;
}

.u_filter {
  -webkit-filter: brightness(1.92);
          filter: brightness(1.92);
  display: block;
  background: red;
}

/* G – Blue */
.g_filter {
  -webkit-filter: brightness(1.08);
          filter: brightness(1.08);
}

/* R – Green */
.r_filter {
  -webkit-filter: brightness(1.45);
          filter: brightness(1.45);
}

/* I – Yellow */
.i_filter {
  -webkit-filter: brightness(0.52);
          filter: brightness(0.52);
}

/* Z – Orange */
.z_filter {
  -webkit-filter: brightness(0.375);
          filter: brightness(0.375);
}

/* Y – Red */
.y_filter {
  -webkit-filter: brightness(1.9);
          filter: brightness(1.9);
}

/* RGB Widget */

/* Red */
.red_filter {
  background-position: center;
  border-radius: 10px;
  position: absolute;
  width: 100%;
  height: 100%;
  background-blend-mode: multiply;
  background-image: url(/epo-investigations-react/static/media/red_filter.87de8389.png);
  mix-blend-mode: screen;
  background-size: 100%;
  display: none;
}

/* Green */
